<template>
  <div>
    <div class="bigbox">
      <div>
        <v-card>
          <v-card-title class="px-7">
            <v-btn v-if="readonly"
                   x-small
                   rounded
                   :color="list.is_used==1?'rgb(19, 166, 113)':'#999999'">
              <div class="px-1">
                {{list.is_used==1?$t('启用'):$t('禁用')}}
              </div>
            </v-btn>
            <mrc_ess_switch :true-value="1"
                            v-else
                            :false-value="0"
                            v-model="list.is_used"
                            :label="list.is_used==1?'启用':'禁用'"></mrc_ess_switch>
          </v-card-title>
          <v-card-text class="px-7">
            <v-row>
              <v-col cols="6">
                <mrc_ess_multiText :label="$t('Name')+'*'"
                                   :readonly="readonly"
                                   :isNational="judegNational('name')"
                                   v-model="list.name">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="6">
                <mrc_ess_prompt :errMsg="$errors('list','change_table_uuid',requiredRule)"
                                :rules="false"
                                :disabled="readonly"
                                :label="$t('Table')+'*'"
                                obj='table_name'
                                v-model="list.change_table_uuid"></mrc_ess_prompt>
              </v-col>
              <v-col cols="6">
                <mrc_ess_prompt :errMsg="$errors('list','change_type_uuid',requiredRule)"
                                :rules="false"
                                :disabled="readonly"
                                :label="$t('Change Type')+'*'"
                                obj='change_type'
                                v-model="list.change_type_uuid"></mrc_ess_prompt>
              </v-col>
              <v-col cols="6">
                <mrc_ess_multiText :label="$t('Business Method')+'*'"
                                   :readonly="readonly"
                                   :isNational="judegNational('bean_method')"
                                   :error-messages="$errors('list','bean_method',requiredRule)"
                                   v-model="list.bean_method">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="6">
                <mrc_ess_multiText :label="$t('Parametrs')+'*'"
                                   :readonly="readonly"
                                   :isNational="judegNational('in_paras_fields')"
                                   :error-messages="$errors('list','in_paras_fields',requiredRule)"
                                   v-model="list.in_paras_fields">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="6">
                <mrc_ess_multiText :label="$t('Server Comment Bean')+'*'"
                                   :error-messages="$errors('list','server_component_bean',requiredRule)"
                                   :readonly="readonly"
                                   :isNational="judegNational('server_component_bean	')"
                                   v-model="list.server_component_bean	">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="12">
                <mrc_ess_multiText :label="$t('remark')"
                                   :readonly="readonly"
                                   :isNational="judegNational('remark')"
                                   v-model="list.remark">
                </mrc_ess_multiText>
              </v-col>
            </v-row>
          </v-card-text>

        </v-card>
      </div>
      <div>
        <mrc_ess_submitBtn @cancel='cancel'
                           v-if="!readonly"
                           @success='success'></mrc_ess_submitBtn>
      </div>
    </div>
  </div>
</template>
<style scoped>
</style> 
<script>  
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import {
  getCodeLogicList, addCodeLogic
  , detailCodeLogic, updateCodeLogic, deleteCodeLogic
} from '@/service/api/codeLogic.js'
export default {
  components: {},
  props: {
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      items: [],
      list: {},
    };
  },
  validations: {
    list: {
      change_table_uuid: {
        required
      },
      change_type_uuid: {
        required
      },
      bean_method: {
        required
      },
      in_paras_fields: {
        required
      },
      server_component_bean: {
        required
      }
    }
  },
  created () { },
  mounted () {
    if (this.$store.state.uuid) {
      this.getDetail()
      // this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },
  methods: {
    async getDetail () {
      const res = await detailCodeLogic({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
      else {

      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑嘛?'
      }).then(res => {
        this.$router.push({
          name: 'codeLogic'
        })
      }).catch(err => { })
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      if (this.$store.state.uuid) {
        updateCodeLogic(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        addCodeLogic(this.list).then(res => {
          if (res.code == 200) {
            this.$Message({ text: this.$t('message_added_successfully') })
            this.$router.push({ name: 'codeLogic' })
          }
          else {
            this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.38)' })
          }
        })
      }

    }
  },
  computed: {

  },
  watch: {},
};
</script>

<style scoped>
</style>
